import React, { useState, useEffect, useMemo } from 'react';
import {
  Box, Typography, TextField, InputAdornment, Button, Card, CardContent, 
  Avatar, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, 
  TablePagination, useTheme, Snackbar, Alert, LinearProgress, Grid, FormControl, 
  InputLabel, Select, MenuItem, Paper, Divider, Chip, Skeleton
} from '@mui/material';
import {
  CalendarToday as CalendarIcon,
  DateRange as DateRangeIcon,
  Assessment as AssessmentIcon, 
  Person as PersonIcon,
  Store as StoreIcon,
  Search as SearchIcon,
  FileDownload as FileDownloadIcon,
  FilterAlt as FilterIcon,
  LocalMall as ProductIcon,
  AttachMoney as MoneyIcon
} from '@mui/icons-material';
import { collection, query, where, getDocs, orderBy, getDoc, doc, Timestamp } from 'firebase/firestore';
import { db } from '../../services/firebase';
import { useAuth } from '../../context/AuthContext';
import { getSubordinateIds, canViewStaffPerformanceReport } from '../../utils/roles';
import { useNavigate } from 'react-router-dom';
import { format, parseISO, isValid } from 'date-fns';

// Helper function to format date
const formatDate = (timestamp) => {
  if (!timestamp) return 'N/A';
  
  if (timestamp && typeof timestamp.toDate === 'function') {
    return format(timestamp.toDate(), 'MMM d, yyyy');
  }
  
  if (timestamp instanceof Date) {
    return format(timestamp, 'MMM d, yyyy');
  }
  
  try {
    const date = new Date(timestamp);
    if (isValid(date)) {
      return format(date, 'MMM d, yyyy');
    }
  } catch (e) {
    console.error("Error parsing date:", e);
  }
  
  return 'Invalid Date';
};

export default function MarketingStaffPerformanceReport() {
  const theme = useTheme();
  const navigate = useNavigate();
  const { userProfile, allUsers, loading: authLoading } = useAuth();
  
  // State
  const [loading, setLoading] = useState(false);
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [searchTerm, setSearchTerm] = useState('');
  const [reportData, setReportData] = useState([]);
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  
  // Summary statistics
  const [totalVisits, setTotalVisits] = useState(0);
  const [totalRetailers, setTotalRetailers] = useState(0);
  const [totalProducts, setTotalProducts] = useState(0);
  const [totalSalesAmount, setTotalSalesAmount] = useState(0);

  // Initialize with current month date range
  useEffect(() => {
    const today = new Date();
    const firstDay = new Date(today.getFullYear(), today.getMonth(), 1);
    const lastDay = new Date(today.getFullYear(), today.getMonth() + 1, 0);
    
    setStartDate(firstDay.toISOString().split('T')[0]);
    setEndDate(lastDay.toISOString().split('T')[0]);
  }, []);

  // Get accessible marketing staff based on user role
  const accessibleStaff = useMemo(() => {
    if (!userProfile || !allUsers) return [];
    
    // Make sure user has permission to view staff performance report
    if (!canViewStaffPerformanceReport(userProfile)) {
      return [];
    }
    
    // Admin can see all marketing staff
    if (userProfile.role === 'Admin') {
      return allUsers.filter(u => u.role === 'MarketingStaff' && u.active);
    }

    // For other managers, get their subordinates
    const myId = userProfile.id;
    const subordinateIds = getSubordinateIds(myId, allUsers);
    
    // Filter for marketing staff among subordinates
    return allUsers.filter(u => 
      u.role === 'MarketingStaff' && 
      u.active && 
      (subordinateIds.includes(u.id) || u.id === myId)
    );
  }, [userProfile, allUsers]);

  // Auto-generate report after initialization
  useEffect(() => {
    if (startDate && endDate && !loading && allUsers?.length > 0) {
      generateReport();
    }
  }, [startDate, endDate, allUsers]);

  // Generate the consolidated report
  const generateReport = async () => {
    if (!startDate || !endDate) {
      setSnackbar({
        open: true,
        message: 'Please select a date range',
        severity: 'warning'
      });
      return;
    }

    setLoading(true);
    
    try {
      // Create date range for query
      const startTimestamp = new Date(startDate + 'T00:00:00');
      const endTimestamp = new Date(endDate + 'T23:59:59');

      // Get all marketing staff IDs based on role access
      const staffIds = accessibleStaff.map(staff => staff.id);
      
      if (staffIds.length === 0) {
        setReportData([]);
        setSnackbar({
          open: true,
          message: 'No marketing staff found',
          severity: 'info'
        });
        setLoading(false);
        return;
      }
      
      // Query all visits within date range
      const visitsQuery = query(
        collection(db, 'visits'),
        where('checkInTime', '>=', startTimestamp),
        where('checkInTime', '<=', endTimestamp),
        orderBy('checkInTime', 'desc')
      );
      
      const visitsSnapshot = await getDocs(visitsQuery);
      let visitsData = visitsSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      
      // Filter for accessible staff
      visitsData = visitsData.filter(visit => staffIds.includes(visit.userId));
      
      // Fetch inventory entries for accurate pricing 
      // Get all inventoryEntryIds from the visits
      const inventoryEntryIds = new Set();
      visitsData.forEach(visit => {
        if (visit.inventoryEntryId) {
          inventoryEntryIds.add(visit.inventoryEntryId);
        }
      });
      
      // Create a map of inventory entries
      const inventoryEntriesMap = {};
      if (inventoryEntryIds.size > 0) {
        const inventoryPromises = Array.from(inventoryEntryIds).map(async (entryId) => {
          try {
            const entryDoc = await getDoc(doc(db, 'inventoryEntries', entryId));
            if (entryDoc.exists()) {
              inventoryEntriesMap[entryId] = entryDoc.data();
            }
          } catch (error) {
            console.error(`Error fetching inventory entry ${entryId}:`, error);
          }
        });
        
        await Promise.all(inventoryPromises);
      }
      
      // Fetch product rates for fallback pricing
      const productRatesQuery = query(collection(db, 'productRates'));
      const productRatesSnapshot = await getDocs(productRatesQuery);
      const productRatesMap = {};
      
      productRatesSnapshot.forEach(doc => {
        const data = doc.data();
        if (data.productId && data.rate) {
          productRatesMap[data.productId] = parseFloat(data.rate);
        }
      });
      
      // Aggregate data by staff member
      const staffPerformanceMap = {};
      let totalVisitCount = 0;
      let uniqueRetailersSet = new Set();
      let productTotal = 0;
      let salesAmountTotal = 0;
      
      // Initialize data structure for each staff
      staffIds.forEach(staffId => {
        staffPerformanceMap[staffId] = {
          staffId,
          staffName: allUsers.find(u => u.id === staffId)?.name || 'Unknown',
          totalVisits: 0,
          uniqueRetailers: new Set(),
          totalProducts: 0,
          totalSalesAmount: 0,
          dealerName: '',
          dealerId: ''
        };
        
        // Get dealer assignment
        const staffUser = allUsers.find(u => u.id === staffId);
        if (staffUser && staffUser.assignedDealerId) {
          staffPerformanceMap[staffId].dealerId = staffUser.assignedDealerId;
        }
      });
      
      // Process all visits data
      for (const visit of visitsData) {
        const staffId = visit.userId;
        if (!staffPerformanceMap[staffId]) continue;
        
        // Count visit
        staffPerformanceMap[staffId].totalVisits++;
        totalVisitCount++;
        
        // Add retailer
        staffPerformanceMap[staffId].uniqueRetailers.add(visit.retailerId);
        uniqueRetailersSet.add(visit.retailerId);
        
        // Calculate products and amounts
        if (Array.isArray(visit.products)) {
          let visitProductCount = 0;
          let visitSalesAmount = 0;
          
          for (const product of visit.products) {
            const count = parseInt(product.count) || 0;
            visitProductCount += count;
            
            // Try to find rate in different sources with priorities:
            // 1. Product rate in the product itself
            // 2. Rate from associated inventory entry
            // 3. Latest product rate from productRates collection
            let rate = null;
            
            // 1. Check product itself
            if (product.rate) {
              rate = parseFloat(product.rate);
            } 
            // 2. Check inventory entry
            else if (visit.inventoryEntryId && inventoryEntriesMap[visit.inventoryEntryId]) {
              const inventoryEntry = inventoryEntriesMap[visit.inventoryEntryId];
              const productEntry = inventoryEntry.productEntries?.find(pe => pe.productId === product.productId);
              if (productEntry && productEntry.rate) {
                rate = parseFloat(productEntry.rate);
              }
            }
            // 3. Check global product rates
            if (rate === null && productRatesMap[product.productId]) {
              rate = productRatesMap[product.productId];
            }
            
            // Calculate amount if we have a rate
            if (rate !== null) {
              visitSalesAmount += count * rate;
            }
          }
          
          staffPerformanceMap[staffId].totalProducts += visitProductCount;
          staffPerformanceMap[staffId].totalSalesAmount += visitSalesAmount;
          
          productTotal += visitProductCount;
          salesAmountTotal += visitSalesAmount;
        }
      }
      
      // Get dealer names for each staff
      const dealerPromises = Object.values(staffPerformanceMap)
        .filter(staff => staff.dealerId)
        .map(async (staff) => {
          try {
            const dealerDoc = await getDoc(doc(db, 'dealers', staff.dealerId));
            if (dealerDoc.exists()) {
              staff.dealerName = dealerDoc.data().name || 'Unknown Dealer';
            }
          } catch (error) {
            console.error(`Error fetching dealer for ${staff.staffName}:`, error);
          }
        });
      
      await Promise.all(dealerPromises);
      
      // Convert to array and calculate percentage metrics
      const performanceArray = Object.values(staffPerformanceMap).map(staff => ({
        ...staff,
        uniqueRetailersCount: staff.uniqueRetailers.size,
        visitPercentage: totalVisitCount ? ((staff.totalVisits / totalVisitCount) * 100).toFixed(1) : 0,
        retailerPercentage: uniqueRetailersSet.size ? ((staff.uniqueRetailers.size / uniqueRetailersSet.size) * 100).toFixed(1) : 0,
        productPercentage: productTotal ? ((staff.totalProducts / productTotal) * 100).toFixed(1) : 0,
        salesPercentage: salesAmountTotal ? ((staff.totalSalesAmount / salesAmountTotal) * 100).toFixed(1) : 0
      }));
      
      // Sort by total visits (descending)
      performanceArray.sort((a, b) => b.totalVisits - a.totalVisits);
      
      // Update state
      setReportData(performanceArray);
      setTotalVisits(totalVisitCount);
      setTotalRetailers(uniqueRetailersSet.size);
      setTotalProducts(productTotal);
      setTotalSalesAmount(salesAmountTotal);
      
      if (performanceArray.length === 0) {
        setSnackbar({
          open: true,
          message: 'No performance data found for the selected period',
          severity: 'info'
        });
      }
    } catch (error) {
      console.error("Error generating report:", error);
      setSnackbar({
        open: true,
        message: 'Error generating report: ' + error.message,
        severity: 'error'
      });
    } finally {
      setLoading(false);
    }
  };

  // Filter data based on search term
  const filteredData = useMemo(() => {
    if (!searchTerm.trim()) return reportData;
    
    const searchLower = searchTerm.toLowerCase();
    return reportData.filter(staff => 
      staff.staffName.toLowerCase().includes(searchLower) ||
      staff.dealerName.toLowerCase().includes(searchLower)
    );
  }, [reportData, searchTerm]);

  // Pagination handlers
  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  // Display rows with pagination
  const displayedRows = filteredData.slice(
    page * rowsPerPage,
    page * rowsPerPage + rowsPerPage
  );

  // Export to CSV
  const exportToCSV = () => {
    if (reportData.length === 0) {
      setSnackbar({
        open: true,
        message: 'No data to export',
        severity: 'warning'
      });
      return;
    }
    
    // Define headers
    const headers = [
      'Marketing Staff',
      'Assigned Dealer',
      'Total Visits',
      'Retailers Reached',
      'Products Sold',
      'Sales Amount',
      'Visit %',
      'Retailer %',
      'Product %',
      'Sales %'
    ];
    
    // Create rows
    const rows = reportData.map(staff => [
      staff.staffName,
      staff.dealerName || 'Not Assigned',
      staff.totalVisits,
      staff.uniqueRetailersCount,
      staff.totalProducts,
      staff.totalSalesAmount.toFixed(2),
      staff.visitPercentage + '%',
      staff.retailerPercentage + '%',
      staff.productPercentage + '%',
      staff.salesPercentage + '%'
    ]);
    
    // Create CSV content
    let csvContent = headers.join(',') + '\n';
    rows.forEach(row => {
      const processedRow = row.map(field => {
        // Quote fields with commas
        if (typeof field === 'string' && (field.includes(',') || field.includes('"'))) {
          return `"${field.replace(/"/g, '""')}"`;
        }
        return field;
      });
      csvContent += processedRow.join(',') + '\n';
    });
    
    // Add report title and date range
    csvContent = `Marketing Staff Performance Report\nDate Range: ${startDate} to ${endDate}\n\n` + csvContent;
    
    // Create and download the file
    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', `marketing_staff_performance_${startDate}_${endDate}.csv`);
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  // Add an access control check at the component level
  if (!canViewStaffPerformanceReport(userProfile)) {
    return (
      <Box sx={{ p: 4, textAlign: 'center' }}>
        <Typography variant="h6" color="error">
          You are not authorized to view staff performance reports.
        </Typography>
      </Box>
    );
  }

  return (
    <Box>
      <Card sx={{ borderRadius: 2, boxShadow: theme.shadows[2], mb: 4 }}>
        <CardContent sx={{ p: 3 }}>
          {/* Header */}
          <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
            <Avatar
              sx={{
                bgcolor: theme.palette.warning.light,
                color: theme.palette.warning.contrastText || '#fff',
                width: 48,
                height: 48,
                mr: 2,
              }}
            >
              <AssessmentIcon />
            </Avatar>
            <Box>
              <Typography variant="h5" fontWeight="bold" color="text.primary">
                Marketing Staff Performance Report
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Consolidated performance metrics for marketing staff
              </Typography>
            </Box>
          </Box>

          {/* Filters */}
          <Paper sx={{ p: 2, mb: 4, borderRadius: 2 }}>
            <Grid container spacing={2} alignItems="center">
              <Grid item xs={12} md={3}>
                <TextField
                  label="Search Staff or Dealer"
                  type="text"
                  size="small"
                  fullWidth
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <SearchIcon sx={{ color: theme.palette.warning.main }} />
                      </InputAdornment>
                    ),
                  }}
                  sx={{ '& .MuiOutlinedInput-root': { borderRadius: 2 } }}
                />
              </Grid>
              <Grid item xs={12} md={3}>
                <TextField
                  label="Start Date"
                  type="date"
                  size="small"
                  fullWidth
                  value={startDate}
                  onChange={(e) => setStartDate(e.target.value)}
                  InputLabelProps={{ shrink: true }}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <CalendarIcon sx={{ color: theme.palette.warning.main }} />
                      </InputAdornment>
                    ),
                  }}
                  sx={{ '& .MuiOutlinedInput-root': { borderRadius: 2 } }}
                />
              </Grid>
              <Grid item xs={12} md={3}>
                <TextField
                  label="End Date"
                  type="date"
                  size="small"
                  fullWidth
                  value={endDate}
                  onChange={(e) => setEndDate(e.target.value)}
                  InputLabelProps={{ shrink: true }}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <DateRangeIcon sx={{ color: theme.palette.warning.main }} />
                      </InputAdornment>
                    ),
                  }}
                  sx={{ '& .MuiOutlinedInput-root': { borderRadius: 2 } }}
                />
              </Grid>
              <Grid item xs={12} md={3}>
                <Box sx={{ display: 'flex', gap: 1 }}>
                  <Button
                    variant="contained"
                    onClick={generateReport}
                    disabled={loading}
                    startIcon={<FilterIcon />}
                    sx={{
                      borderRadius: '2rem',
                      bgcolor: theme.palette.warning.main,
                      '&:hover': { bgcolor: theme.palette.warning.dark }
                    }}
                  >
                    Apply Filters
                  </Button>
                  <Button
                    variant="outlined"
                    onClick={exportToCSV}
                    disabled={loading || reportData.length === 0}
                    startIcon={<FileDownloadIcon />}
                    sx={{
                      borderRadius: '2rem',
                      borderColor: theme.palette.warning.main,
                      color: theme.palette.warning.main,
                      '&:hover': { borderColor: theme.palette.warning.dark }
                    }}
                  >
                    Export CSV
                  </Button>
                </Box>
              </Grid>
            </Grid>
          </Paper>

          {/* Summary Cards */}
          <Box sx={{ mb: 4 }}>
            <Grid container spacing={3}>
              <Grid item xs={12} sm={6} md={3}>
                <Paper
                  elevation={0}
                  sx={{
                    p: 2,
                    borderRadius: 2,
                    bgcolor: theme.palette.warning.light,
                    color: theme.palette.warning.contrastText,
                    textAlign: 'center',
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center'
                  }}
                >
                  <Typography variant="h3" sx={{ fontWeight: 700, mb: 1 }}>
                    {totalVisits}
                  </Typography>
                  <Typography variant="body1">Total Visits</Typography>
                </Paper>
              </Grid>
              <Grid item xs={12} sm={6} md={3}>
                <Paper
                  elevation={0}
                  sx={{
                    p: 2,
                    borderRadius: 2,
                    bgcolor: theme.palette.success.light,
                    color: theme.palette.success.contrastText,
                    textAlign: 'center',
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center'
                  }}
                >
                  <Typography variant="h3" sx={{ fontWeight: 700, mb: 1 }}>
                    {totalRetailers}
                  </Typography>
                  <Typography variant="body1">Retailers Reached</Typography>
                </Paper>
              </Grid>
              <Grid item xs={12} sm={6} md={3}>
                <Paper
                  elevation={0}
                  sx={{
                    p: 2,
                    borderRadius: 2,
                    bgcolor: theme.palette.info.light,
                    color: theme.palette.info.contrastText,
                    textAlign: 'center',
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center'
                  }}
                >
                  <Typography variant="h3" sx={{ fontWeight: 700, mb: 1 }}>
                    {totalProducts}
                  </Typography>
                  <Typography variant="body1">Products Sold</Typography>
                </Paper>
              </Grid>
              <Grid item xs={12} sm={6} md={3}>
                <Paper
                  elevation={0}
                  sx={{
                    p: 2,
                    borderRadius: 2,
                    bgcolor: theme.palette.primary.light,
                    color: theme.palette.primary.contrastText,
                    textAlign: 'center',
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center'
                  }}
                >
                  <Typography variant="h3" sx={{ fontWeight: 700, mb: 1 }}>
                    â‚¹{totalSalesAmount.toFixed(2)}
                  </Typography>
                  <Typography variant="body1">Total Sales Amount</Typography>
                </Paper>
              </Grid>
            </Grid>
          </Box>

          {/* Results Table */}
          {loading ? (
            <Box sx={{ mb: 2 }}>
              <LinearProgress />
              {/* Skeleton placeholders for table */}
              <TableContainer>
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell sx={{ fontWeight: 'bold' }}>Marketing Staff</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Assigned Dealer</TableCell>
                      <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Total Visits</TableCell>
                      <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Retailers Reached</TableCell>
                      <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Products Sold</TableCell>
                      <TableCell sx={{ fontWeight: 'bold', textAlign: 'right' }}>Sales Amount</TableCell>
                      <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Contribution %</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {[...Array(5)].map((_, i) => (
                      <TableRow key={i}>
                        <TableCell>
                          <Box sx={{ display: 'flex', alignItems: 'center' }}>
                            <Skeleton variant="circular" width={36} height={36} sx={{ mr: 1.5 }} />
                            <Skeleton variant="text" width={120} />
                          </Box>
                        </TableCell>
                        <TableCell><Skeleton variant="text" width={150} /></TableCell>
                        <TableCell align="center"><Skeleton variant="text" width={60} /></TableCell>
                        <TableCell align="center"><Skeleton variant="text" width={60} /></TableCell>
                        <TableCell align="center"><Skeleton variant="text" width={60} /></TableCell>
                        <TableCell align="right"><Skeleton variant="text" width={80} /></TableCell>
                        <TableCell align="center"><Skeleton variant="rounded" width={60} height={24} /></TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            </Box>
          ) : reportData.length === 0 ? (
            <Box sx={{ textAlign: 'center', p: 4, bgcolor: 'rgba(0,0,0,0.02)', borderRadius: 2 }}>
              <Typography variant="body1" color="text.secondary">
                No performance data found for the selected criteria
              </Typography>
            </Box>
          ) : (
            <TableContainer>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell sx={{ fontWeight: 'bold' }}>Marketing Staff</TableCell>
                    <TableCell sx={{ fontWeight: 'bold' }}>Assigned Dealer</TableCell>
                    <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Total Visits</TableCell>
                    <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Retailers Reached</TableCell>
                    <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Products Sold</TableCell>
                    <TableCell sx={{ fontWeight: 'bold', textAlign: 'right' }}>Sales Amount</TableCell>
                    <TableCell sx={{ fontWeight: 'bold', textAlign: 'center' }}>Contribution %</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {displayedRows.length === 0 ? (
                    <TableRow>
                      <TableCell colSpan={7} align="center">
                        <Typography variant="body2" color="text.secondary">
                          No matching results found
                        </Typography>
                      </TableCell>
                    </TableRow>
                  ) : (
                    displayedRows.map((staff) => (
                      <TableRow 
                        key={staff.staffId}
                        sx={{ '&:hover': { bgcolor: 'rgba(0,0,0,0.04)' } }}
                        onClick={() => navigate(`/sales/staff/${staff.staffId}/details`)}
                        style={{ cursor: 'pointer' }}
                      >
                        <TableCell>
                          <Box sx={{ display: 'flex', alignItems: 'center' }}>
                            <Avatar 
                              sx={{ 
                                width: 36, height: 36, mr: 1.5,
                                bgcolor: theme.palette.warning.main 
                              }}
                            >
                              {staff.staffName?.charAt(0) || '?'}
                            </Avatar>
                            <Typography variant="body2" fontWeight={500}>
                              {staff.staffName}
                            </Typography>
                          </Box>
                        </TableCell>
                        <TableCell>
                          <Box sx={{ display: 'flex', alignItems: 'center' }}>
                            {staff.dealerName ? (
                              <>
                                <StoreIcon sx={{ fontSize: 20, mr: 1, color: 'text.secondary' }} />
                                <Typography variant="body2">{staff.dealerName}</Typography>
                              </>
                            ) : (
                              <Typography variant="body2" color="text.secondary">
                                Not Assigned
                              </Typography>
                            )}
                          </Box>
                        </TableCell>
                        <TableCell align="center">
                          <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                            <Typography variant="body2" fontWeight={500}>
                              {staff.totalVisits}
                            </Typography>
                            <Typography variant="caption" color="text.secondary" sx={{ ml: 0.5 }}>
                              ({staff.visitPercentage}%)
                            </Typography>
                          </Box>
                        </TableCell>
                        <TableCell align="center">
                          <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                            <Typography variant="body2" fontWeight={500}>
                              {staff.uniqueRetailersCount}
                            </Typography>
                            <Typography variant="caption" color="text.secondary" sx={{ ml: 0.5 }}>
                              ({staff.retailerPercentage}%)
                            </Typography>
                          </Box>
                        </TableCell>
                        <TableCell align="center">
                          <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                            <ProductIcon sx={{ color: theme.palette.info.main, fontSize: 18, mr: 0.5 }} />
                            <Typography variant="body2" fontWeight={500}>
                              {staff.totalProducts}
                            </Typography>
                            <Typography variant="caption" color="text.secondary" sx={{ ml: 0.5 }}>
                              ({staff.productPercentage}%)
                            </Typography>
                          </Box>
                        </TableCell>
                        <TableCell align="right">
                          <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'flex-end' }}>
                            <MoneyIcon sx={{ color: theme.palette.success.main, fontSize: 18, mr: 0.5 }} />
                            <Typography variant="body2" fontWeight={500}>
                              â‚¹{staff.totalSalesAmount.toFixed(2)}
                            </Typography>
                          </Box>
                        </TableCell>
                        <TableCell align="center">
                          <Chip 
                            label={`${staff.salesPercentage}%`} 
                            color={parseFloat(staff.salesPercentage) > 15 ? "success" : "default"}
                            size="small"
                            sx={{ 
                              borderRadius: '14px', 
                              fontWeight: 500,
                              minWidth: '60px'
                            }}
                          />
                        </TableCell>
                      </TableRow>
                    ))
                  )}
                </TableBody>
              </Table>
              <TablePagination
                rowsPerPageOptions={[10, 25, 50]}
                component="div"
                count={filteredData.length}
                rowsPerPage={rowsPerPage}
                page={page}
                onPageChange={handleChangePage}
                onRowsPerPageChange={handleChangeRowsPerPage}
              />
            </TableContainer>
          )}
        </CardContent>
      </Card>

      {/* Snackbar for notifications */}
      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={() => setSnackbar({ ...snackbar, open: false })}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert 
          onClose={() => setSnackbar({ ...snackbar, open: false })} 
          severity={snackbar.severity}
          sx={{ width: '100%' }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  );
} 